import React from 'react';
import Image from '../components/assets/logo.jpg';
import './Header.css';
import {Navbar,Nav} from 'react-bootstrap';
// import {Link} from "react-router-dom";
import  {NavLink} from 'react-router-dom';



const Header =()=>{


return (
 <Navbar sticky="top" className="headnav" expand="lg" >
  <Navbar.Brand href="/docar/"><img className="header__logoImage" src={Image} alt="twn-logo" /></Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" id="navbar-toggler-icon" />
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="elements" id="items"  >
      <NavLink to="/docar/" id="element1" exact activeClassName="navbar__link--active"   >Home</NavLink>
      <NavLink to="/docar/services" id="element2" exact activeClassName="navbar__link--active"  >Service</NavLink>
      <NavLink to="/docar/contact" id="element3" activeClassName="navbar__link--active">Contact</NavLink>
      <NavLink to="/docar/login" id="element4"  activeClassName="navbar__link--active">Login</NavLink>
      <NavLink to="/docar/signup" id="element5" activeClassName="navbar__link--active" >SignUp</NavLink>
    </Nav>
    
  </Navbar.Collapse>
</Navbar> 
)
}




export default Header; 