import React from "react";
import './App.css';


import Homepage from './components/Homepage';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Service from './components/Service';
import Contact from './components/Contact';
import SignUp from './components/SignUp';
import Login from './components/Login';
import Feedback from './components/FeedbackHome';

 
const App=()=> {
  
  return ( 
    <Router>
        <div>
         {/* <Homepage /> */}
         <Switch>
           <Route path="/docar/" exact strict component={Homepage} />
           <Route path="/docar/services" exact strict component={Service} />
           <Route path="/docar/contact" exact strict component={Contact}/>
           <Route path="/docar/signup" exact strict component={SignUp}/>
           <Route path="/docar/login" exact strict component={Login}/>
           <Route path="/docar/feedback" eact strict component={Feedback} />
          </Switch>
         </div>
   </Router>    
  );
} 

export default App;

